export default theme => ({
  root: {
    width: '60%',
    margin: '0 auto',
  },
  appBar: {
    position: 'relative',
    background: 'linear-gradient(#333, #222)',
    color: 'gray'
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
  list: {
    background: 'linear-gradient(#333, #222)',
    height: '100%',
    color: 'gray'
  },
  listItemText: {
    color: 'gray'
  },
  question: {
    color: 'white'
  }
});