export default theme => ({
  root: {
    position: 'relative',
    height: '100%',
    width: '100%',
    background: 'linear-gradient(#1b1b1b, #111)',
    borderRadius: theme.shape.borderRadius,
    boxShadow: '0 0 0 1px #272727',
    padding: theme.spacing(2.4),
    overflow: 'hidden',
    display: 'flex'
  },
  inputRoot: {
    color: 'inherit',
    width: '80%'
  },
  inputInput: {
    color: 'gray',
    boxShadow: 'inset 0 0 0 1px #272727',
    borderRadius: theme.spacing(1, 0, 0, 1),
    padding: theme.spacing(0, 2, 0, 2),
    height: '100%',
    background: 'linear-gradient(#333, #222)',
    '&:focus': {
      background: '#222922',
      animation: '$glow 800ms ease-out infinite alternate'
    }
  },
  searchIcon: {
    background: 'linear-gradient(#333, #222)',
    color: 'white',
    width: '20%',
    height: theme.spacing(6),
    borderLeft: '1px solid #101010',
    boxShadow: 'inset 0 0 0 1px #272727',
    borderRadius: theme.spacing(0, 1, 1, 0),
    '&:hover': {
      color: '#5f5'
    }
  },
  '@keyframes glow': {
    '0%': {
      borderColor: '#393',
      boxShadow: '0 0 5px rgba(0,255,0,.2), inset 0 0 5px rgba(0,255,0,.1), 0 2px 0 #000',
    },
    '100%': {
      borderColor: '#6f6',
      boxShadow: '0 0 10px rgba(0,255,0,.6), inset 0 0 10px rgba(0,255,0,.4), 0 2px 0 #000'
    }
  }
});