import React from 'react';
import { connect } from 'react-redux';
import { withStyles } from "@material-ui/core";
import SearchBar from "components/SearchBar";
import ResultPage from "views/ResultPage";
import styles from "./styles";
import * as actions from './actions';


class SearchPage extends React.Component {

  render() {
    const { classes, open, handleOpen, handleClose, results  } = this.props;
    return (
      <div className={ classes.root }>
        <section className={ classes.searchBar }>
          <div className={classes.logo}/>
          <SearchBar onClick={ handleOpen }/>
          <div className={ classes.drawLine }/>
        </section>
        <ResultPage open={ open } handleClose={ handleClose } results={results}/>
      </div>
    );
  }

}

const mapState = (state) => ({
  open: state.getIn(['search', 'open']),
  results: state.getIn(['search', 'results'])
});

const mapDispatch = (dispatch) => ({
  handleOpen() {
    dispatch(actions.getSearchClickedAction());
  },
  handleClose() {
    dispatch(actions.getCloseDialogAction());
  }
});

export default connect(mapState, mapDispatch)(withStyles(styles)(SearchPage));