import React from 'react';
import {
  withStyles, Dialog, Slide, AppBar,
  Toolbar, Button, IconButton, Typography,
  List, ListItem, ListItemText, Divider
} from "@material-ui/core";
import CloseIcon from '@material-ui/icons/Close';
import AddBoxIcon from '@material-ui/icons/AddBox';
import styles from "./styles";


const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

class ResultPage extends React.PureComponent {

  render() {
    const { classes, open, handleClose, results } = this.props;
    return (
      <Dialog fullScreen open={ open } onClose={ handleClose } TransitionComponent={Transition} className={classes.root}>
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              Results
            </Typography>
            <IconButton autoFocus color="inherit" onClick={() => alert('Website is still under construction.')}  style={{textTransform: 'none',}}>
              <AddBoxIcon/>
            </IconButton>
          </Toolbar>
        </AppBar>
        <List className={classes.list}>
          {
            results.map(result => (
                <ListItem button key={result.get('id')}>
                  <a href={result.get('link')}>
                    <ListItemText className={classes.listItemText} primary={
                      <Typography component="h1"
                        className={classes.question}
                      >
                        Q: {result.get('question')}
                      </Typography>
                    } secondary={
                      <Typography component="p">
                        {result.get('solution')}
                      </Typography>
                    }/>
                  </a>
                </ListItem>
              )
            )
          }
        </List>
      </Dialog>
    );
  }

}

export default withStyles(styles)(ResultPage);