import React from 'react';
import { withStyles } from "@material-ui/core";
import tinymce from "tinymce/tinymce";
import "tinymce/themes/silver/theme";
import "tinymce/plugins/table";
import "tinymce/plugins/fullscreen";
import "tinymce/plugins/preview";
import "tinymce/plugins/codesample";
import "tinymce/plugins/image";
import "tinymce/plugins/emoticons";
import "tinymce/plugins/lists";
import "tinymce/plugins/advlist";
import "tinymce/plugins/quickbars";
import "tinymce/plugins/link";
import styles from "./styles";


class TinymceEditor extends React.PureComponent {

  constructor(props) {
    super(props);
    this.state = { editor: null };
  }

  async componentDidMount() {
    const { onChange } = this.props;
    tinymce.init({
      selector: "#tinymce_1024_id",
      skin_url: '/static/skins/ui/oxide-dark',
      theme: 'silver',
      language_url : '/static/langs/zh_CN.js',
      language:'zh_CN',
      plugins: 'table fullscreen preview codesample image emoticons lists advlist quickbars link',
      menubar: false,
      statusbar: true,
      toolbar: 'preview | formatselect bold italic underline strikethrough | forecolor backcolor | image emoticons link | alignleft aligncenter | outdent indent | imageUpload quicklink blockquote table numlist bullist codesample | fullscreen', 
      setup: editor => {
        this.setState({ editor: editor });
        editor.on('keyup change', () => {
          const content = editor.getContent();
          onChange(content);
        });
      }
    });
    
  }

  componentWillUnmount() {
    tinymce.remove(this.state.editor);
  }

  render () {
    const { classes } = this.props;
    return (
      <textarea id="tinymce_1024_id" className={classes.root}/>
    )

  }

}

export default withStyles(styles)(TinymceEditor);